body {
  margin: 0;
  background: #056679;
  font-family: sans-serif;
}
.container {
  width: 350px;
  text-align: center;
  margin: auto;
  margin-top: 30px;
}
h1 {
  margin: 0;
  margin-bottom: 15px;
  color: #0c7588;
}
.input-section {
  background-color: #055565;
  padding: 18px;
  border-radius: 10px;
  box-shadow: 0 7px 6px #0000003b;
}
.input-section input {
  border-radius: 5px;
  width: 100%;
  border: 1px solid #106879;
  padding: 10px;
  box-sizing: border-box;
  background-color: #055565;
  outline: none;
  color: #08869e;
}
input::placeholder {
  color: #08869e;
}
ul {
  text-align: left;
  padding: 0;
}
ul li {
  list-style: none;
  background-color: #055565;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  color: #08869e;
  box-shadow: 0 7px 6px #0000003b;
  display: flex;
}
ul li i {
  flex: 1;
  text-align: right;
  cursor: pointer;
}
.popup {
  background-color: #fff;
  width: 200px;
  height: 50px;
}
